import { Button, Image, Row, Select, Form, Input, Spin } from "antd";
import swal from "sweetalert";

//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { useState } from 'react';
import { uploadBanner, getNewsAction } from "../../../redux/actions";
//
export default function AdminAddBanner() {
    const dispatch = useDispatch();
    const [selectedFile, setSelectedFile] = useState(null);
    const getImage = useSelector(state => state.adminReducer);
    const { loading_banner, banner, newsList } = getImage
    function setFile(e) {
        setSelectedFile(e.target.files)
    }
    function addBanner(values) {
        if (selectedFile.length === 0) {
            swal("Vui lòng chọn ảnh", "", "error")
        } else {
            const types = ['image/png', 'image/jpeg', 'image/gif']
            if (selectedFile[0].size > 5000000) {
                swal("Kích Thước Ảnh Quả Lớn", "vui lòng chọn file ảnh có kích thước nhỏ hơn 5mb", "warning")
            }
            if (types.every(type => selectedFile[0].type !== type)) {
                swal("Sai Định Dạng", "vui lòng chọn file ảnh theo định dạng PNG,JPG,JPEG", "warning")
            }
            else {
                const formData = new FormData();
                formData.append("file", selectedFile[0]);
                formData.append("upload_preset", "tccvzctk");
                dispatch(uploadBanner(formData, values));
            }
        }
    }
    return (
        <>
            <Row>
                <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                    <h4 style={{ marginTop: '30px' }}>Thêm Banner</h4>
                </div>
                <div className="content3">
                    <div className="profile-holder">
                        <div class="form-group files">
                            <label>Ảnh Bìa</label>
                            <input type="file" className="form-control" onChange={setFile} accept="image/*" />
                        </div>
                        <Form onFinish={(values) => addBanner(values)} layout="vertical">
                            <Form.Item
                                label="Tin Tức"
                                name="id_tintuc"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng chọn tin tức...',
                                    },
                                ]}
                            >
                                <Select placeholder="Chọn tin tức..." onClick={() => { dispatch(getNewsAction()) }}>
                                    {newsList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id_tintuc}>{item.tieude}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                label="Tiêu Đề Ảnh Bìa"
                                name="title_banner"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập tiêu đề ảnh bìa!',
                                    },
                                ]}
                            >
                                <Input.TextArea showCount maxLength={20} />
                            </Form.Item>
                            <Form.Item
                                label="Tiêu Đề Phụ Ảnh Bìa"
                                name="sub_title_banner"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập tiêu đề phụ của ảnh bìa!',
                                    },
                                ]}
                            >
                                <Input.TextArea showCount maxLength={20} />
                            </Form.Item>
                            <Form.Item
                                label="Hiệu Ứng Tiêu Đề"
                                name="title_effect"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng chọn hiệu ứng...',
                                    },
                                ]}
                            >
                                <Select placeholder="Chọn hiệu ứng tiêu đề...">
                                    <Select.Option value="fadeInUp">Mờ dần lên</Select.Option>
                                    <Select.Option value="rollIn">Cuộn vào</Select.Option>
                                    <Select.Option value="rotateInDownLeft">Xoay xuống bên trái</Select.Option>
                                </Select>
                            </Form.Item>
                            <Form.Item
                                label="Hiệu Ứng Tiêu Đề Phụ"
                                name="sub_title_effect"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng chọn hiệu ứng...',
                                    },
                                ]}
                            >
                                <Select placeholder="Chọn hiệu ứng tiêu đề phụ...">
                                    <Select.Option value="fadeInDown">Mờ dần xuống</Select.Option>
                                    <Select.Option value="lightSpeedIn">Lướt nhanh vào</Select.Option>
                                    <Select.Option value="rotateInUpRight">Xoay lên bên phải</Select.Option>
                                </Select>
                            </Form.Item>
                            <Form.Item
                                label="Hiệu Ứng Button"
                                name="button_effect"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng chọn hiệu ứng...',
                                    },
                                ]}
                            >
                                <Select placeholder="Chọn hiệu ứng button...">
                                    <Select.Option value="zoomIn">Zoom dần lên</Select.Option>
                                    <Select.Option value="slideInUp">Trượt lên</Select.Option>
                                    <Select.Option value="rotateIn">Xoay một vòng</Select.Option>
                                </Select>
                            </Form.Item>
                            {loading_banner ? <Spin size='large' /> : (
                                <Form.Item>
                                    <Button type='primary' htmlType="submit">Thêm Ảnh Bìa</Button>
                                </Form.Item>
                            )}
                        </Form>
                        <div>
                            <Image src={banner} width={200} />
                        </div>
                    </div>
                </div>
            </Row>
        </>
    )
}