import React from 'react'
//REDUX && REACT
import { getNewsHomeAction, removeNewsHomeAction } from "../../../redux/actions";
import { useSelector, useDispatch } from "react-redux";
import { useEffect } from 'react';
//HISTORY
import history from '../../../utils/history';
import { ROUTERS } from '../../../constants/router';

function NewsCard() {
    const dispatch = useDispatch();
    const getNewsContent = useSelector(state => state.adminReducer);
    const { newsHomeList } = getNewsContent;
    useEffect(() => {
        dispatch(getNewsHomeAction());
        return () => {
            dispatch(removeNewsHomeAction())
        }
    }, []);
    newsHomeList.forEach(item => {
        item.ngayviet = item.ngayviet.replace("T", " ").substr(0, 19)
    })
    return (
        <div className="container-news">
            <div className="title-best-product">
                <div className="title-line"></div>
                <h3 className="title-heading">TIN TỨC MỚI NHẤT</h3>
                <div className="title-line"></div>
            </div>
            <div className="row news-contain">
                {newsHomeList.map((item, index) => {
                    return (
                        <div className="news-card" key={index}>
                            <div className="img-news-card">
                                <img src={item.anhbia} alt="loading" />
                            </div>
                            <div className="content-news-card">
                                <div>
                                    <h3>{item.tieude}</h3>
                                    <p className="news-summary">{item.tomtat}</p>
                                    <a onClick={()=>history.push(`/news/${item.id_tintuc}`)} >Đọc Thêm</a>
                                </div>
                            </div>
                            <div className="news-card-title">{item.tieude}</div>
                        </div>
                    )
                })}
            </div>
            <div className="load-more-button">
                    <button className="show-hide-button">
                        <span className="review-show-button" onClick={()=>history.push(ROUTERS.NEWS)}>Xem thêm</span>
                    </button>
                </div>
        </div>
    )
}

export default NewsCard
