//CSS
import { Tabs, Form, Input, Button, InputNumber, Select, Row, Spin } from 'antd';
import { Editor } from '@tinymce/tinymce-react';
import swal from 'sweetalert';
import './styles.css'
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import {
    getShoeTypeAction,
    createGalleryAction,
    upLoadFile,
    getShoeAction,
    getShoeColorAction,
    getShoeSizeAction,
    createShoeAction,
    createShoeDetailAction,
    getDistinctColorAction,
    removeShoeAction,
    removeShoeTypeAction,
    removeShoeColorAction,
    removeShoeSizeAction,
    removeShoeColorActionDistinct,
}
    from "../../../../redux/actions";
import { useEffect, useState } from 'react';

function AddProduct() {
    const dispatch = useDispatch();
    const getShoeType = useSelector(state => state.adminReducer);
    const getShoe = useSelector(state => state.productReducer);
    const { shoeTypeList, shoeColorList, shoeSizeList, distinctColor, loading, error } = getShoeType;
    const { shoeList } = getShoe;
    const [selectedFile, setSelectedFile] = useState([]);
    const [content, setContent] = useState('');
    useEffect(() => {
        dispatch(getShoeAction())
        dispatch(getShoeTypeAction());
        dispatch(getShoeColorAction());
        dispatch(getShoeSizeAction());
        return () => {
            dispatch(removeShoeAction());
            dispatch(removeShoeTypeAction());
            dispatch(removeShoeColorAction());
            dispatch(removeShoeSizeAction());
            dispatch(removeShoeColorActionDistinct());
        }
    }, []);
    const { TabPane } = Tabs;
    function handleEditorChange(content, editor) {
        setContent(content)
    }
    const onFinish = (values) => {
        let check = false;
        if(shoeList.some(item => item.tenmagiay.toLowerCase() === values.tenmagiay.toLowerCase())){
            swal("Mã Giày Đã Tồn Tại","Vui lòng nhập mã giày khác","warning")
            check = true;
        }
        if (content === "") {
            swal('Vui lòng nhập nội dung', '', 'warning')
            check = true;
        }
        if (selectedFile.length === 0) {
            swal('Vui lòng chọn ảnh', '', 'warning')
            check = true;
        } 
        if(check === false){
            const types = ['image/png', 'image/jpeg', 'image/gif']
            if (selectedFile[0].size > 5000000) {
                swal("Kích Thước Ảnh Quả Lớn", "vui lòng chọn file ảnh có kích thước nhỏ hơn 5mb", "warning")
            }
            if (types.every(type => selectedFile[0].type !== type)) {
                swal("Sai Định Dạng", "vui lòng chọn file ảnh theo định dạng PNG,JPG,JPEG", "warning")
            } 
            else {
                let hinhanh = selectedFile[0].name
                const newValues = {
                    ...values,
                    hinhanh,
                    noidung: content,
                }
                dispatch(createShoeAction(newValues))
                dispatch(upLoadFile(selectedFile))
            }    
        }
    };
    const onFinishDetail = (values) => {
        dispatch(createShoeDetailAction(values))
    };
    function checkSize() {
        let check = false
        for (let i = 0; i < selectedFile.length; i++) {
            if (selectedFile[i].size > 5000000) {
                check = true;
            }
        }
        if (check) {
            swal("Kích Thước Ảnh Quả Lớn", "vui lòng chọn file ảnh có kích thước nhỏ hơn 5mb", "warning")
            return false;
        }
        else {
            return true;
        }
    }
    function checkType() {
        const types = ['image/png', 'image/jpeg', 'image/gif']
        let check = false
        for (let i = 0; i < selectedFile.length; i++) {
            if (types.every(type => selectedFile[i].type !== type)) {
                check = true;
            }
        }

        if (check) {
            swal("Sai Định Dạng", "vui lòng chọn file ảnh theo định dạng PNG,JPG,JPEG", "warning")
            return false;
        }
        else {
            return true;
        }
    }
    const onFinishGallery = (values) => {
        if (checkType() && checkSize()) {
            let image = [];
            for (let i = 0; i < selectedFile.length; i++) {
                image.push(selectedFile[i].name)
            }
            const newValues = {
                ...values,
                image,
            };
            dispatch(createGalleryAction(newValues))
            dispatch(upLoadFile(selectedFile))
        }
    };

    function handleChange(values) {
        dispatch(getDistinctColorAction(values));
    }

    function setFile(e) {
        setSelectedFile(e.target.files)
    }
    return (
        <>
            <Row>
                <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                    <h4>Thêm Giày</h4>
                </div>
                <div className="content3">
                    <div className="profile-holder">

                        <Tabs defaultActiveKey="1" onChange={() => dispatch(getShoeAction())}>
                            <TabPane tab="Thêm Giày" key="1">
                                <Form
                                    name="createshoe"
                                    initialValues={{
                                        remember: true,
                                    }}
                                    onFinish={onFinish}
                                >
                                    <Form.Item
                                        name="tengiay"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập tên giày...',
                                            },
                                        ]}
                                    >
                                        <Input placeholder="Nhập tên giày..." />
                                    </Form.Item>

                                    <Form.Item

                                        name="tenmagiay"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập tên mã giày...',
                                            },
                                        ]}
                                    >
                                        <Input placeholder="Nhập tên mã giày..." />
                                    </Form.Item>

                                    <Form.Item
                                        label="Giá Bán: "
                                        name="giaban"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập giá bán...',
                                            },

                                        ]}
                                    >
                                        <InputNumber
                                            style={{ width: 500 }}
                                            min={1}
                                            max={10000000}
                                            formatter={value => `VNĐ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                            parser={value => value.replace(/\VNĐ\s?|(,*)/g, '')}
                                        />
                                    </Form.Item>

                                    <Editor
                                        apiKey='vay56zrvh9veuhcbway7pupzaoycpwvx06k3t4xzzsv3lfgv'
                                        initialValue="<p>Đây là nội dung mặc định</p>"
                                        init={{
                                            height: 500,
                                            menubar: true,
                                            // ariaHidden: true,
                                            plugins: [
                                                'advlist autolink lists link image charmap print preview anchor',
                                                'searchreplace visualblocks code fullscreen',
                                                'insertdatetime media table paste code help wordcount'
                                            ],
                                            toolbar:
                                                'undo redo | formatselect | bold italic backcolor | \
                                                alignleft aligncenter alignright alignjustify | \
                                                bullist numlist outdent indent | removeformat | help'
                                        }}
                                        onEditorChange={handleEditorChange}
                                    />

                                    <Form.Item
                                        name="id_kieugiay"
                                        style={{ marginTop: '20px' }}
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng chọn kiểu giày...',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Chọn kiểu giày...">
                                            {shoeTypeList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id_kieugiay}>{item.tenkieugiay}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>

                                    <Form.Item
                                        name="tinhtrang"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng chọn tình trạng...',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Chọn tình trạng...">
                                            <Select.Option value="1">Kích Hoạt</Select.Option>
                                            <Select.Option value="0">Không Kích Hoạt</Select.Option>
                                        </Select>
                                    </Form.Item>

                                    <div class="form-group files">
                                        <label>Hình Ảnh Đại Diện</label>
                                        <input type="file" class="form-control" onChange={setFile} accept="image/*" />
                                    </div>
                                    {loading
                                        ? <Spin size="large" />
                                        : error
                                            ? <div>{error}</div>
                                            : (
                                                <Form.Item >
                                                    <Button type="primary" htmlType="submit">Thêm Giày</Button>
                                                </Form.Item>
                                            )
                                    }
                                </Form>
                            </TabPane>
                            <TabPane tab="Thêm Chi Tiết Giày" key="2">
                                <Form
                                    name="createshoedetail"
                                    initialValues={{
                                        remember: true,
                                    }}
                                    onFinish={onFinishDetail}
                                >

                                    <Form.Item

                                        name="id_giay"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập tên mã giày!',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Nhập tên mã giày...">
                                            {shoeList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id_giay}>{item.tenmagiay}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>

                                    <Form.Item
                                        name="id_color"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập màu giày!',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Nhập màu giày...">
                                            {shoeColorList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id_color}>{item.color}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>

                                    <Form.Item
                                        name="id_size"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập size giày!',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Nhập size giày...">
                                            {shoeSizeList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id_size}>{item.sosize}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>

                                    <Form.Item
                                        name="soluong"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập số lượng...',
                                            },
                                        ]}
                                    >
                                        <InputNumber placeholder="Nhập số lượng..." style={{ width: 'auto' }} />
                                    </Form.Item>

                                    <Form.Item>
                                        <Button type="primary" htmlType="submit">Thêm Chi Tiết Giày</Button>
                                    </Form.Item>
                                </Form>
                            </TabPane>
                            <TabPane tab="Thêm Ảnh Giày" key="3">
                                <Form
                                    name="galleryProduct"
                                    initialValues={{
                                        remember: true,
                                    }}
                                    onFinish={onFinishGallery}
                                >
                                    <Form.Item
                                        name="id_giay"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập tên mã giày!',
                                            },
                                        ]}
                                    >
                                        <Select placeholder="Nhập tên mã giày..." onChange={handleChange}>
                                            {shoeList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id_giay}>{item.tenmagiay}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>
                                    {distinctColor.length !== 0 && (
                                        <Form.Item
                                            name="id_color"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Vui lòng nhập màu giày!',
                                                },
                                            ]}
                                        >
                                            <Select placeholder="Chọn màu giày...">
                                                {distinctColor.map((item, index) => {
                                                    return <Select.Option key={index} value={item.id_color}>{item.color}</Select.Option>
                                                })}
                                            </Select>
                                        </Form.Item>
                                    )}
                                    <div class="form-group files">
                                        <label>Thêm Thư Viện Ảnh</label>
                                        <input type="file" class="form-control" multiple onChange={setFile} accept="image/*" />
                                    </div>
                                    {loading
                                        ? <Spin size="large" />
                                        : error
                                            ? <div>{error}</div>
                                            : (
                                                <Form.Item>
                                                    <Button type="primary" htmlType="submit">Thêm Ảnh Giày</Button>
                                                </Form.Item>
                                            )
                                    }
                                </Form>
                            </TabPane>
                        </Tabs>
                    </div>
                </div>

            </Row>
        </>
    )
}
export default AddProduct