import React, { useEffect, useState } from 'react'
import "antd/dist/antd.css";
import { Form, Input, Select } from 'antd';
import { ArrowRightOutlined, ArrowLeftOutlined } from '@ant-design/icons';
//Components
import { ROUTERS } from '../../../constants/router'
import history from '../../../utils/history'
//Redux
import { useSelector, useDispatch } from "react-redux";
import { createInvoice, removeCityAction, getCityAction, getDistrictAction, getWardAction, removeWardAction, removeDistrictAction } from '../../../redux/actions'

function ShippingForm() {
    const getUser = useSelector(state => state.userReducer);
    const getCart = useSelector(state => state.cartReducer);
    const getLoading = useSelector(state => state.checkoutReducer);
    const { userInfo, cityList, districtList, wardList } = getUser;
    const { cartItems } = getCart;
    const { loading } = getLoading;

    useEffect(() => {
        dispatch(getCityAction());
        return () => {
            dispatch(removeCityAction());
            dispatch(removeDistrictAction());
            dispatch(removeWardAction());
        }
    }, []);

    if (cartItems.length === 0) {
        history.push(ROUTERS.CHECKOUT_STEP_1)
    }
    let totalPrice = 0;
    function calculateTotal(arr) {
        totalPrice = arr.reduce((acc, val) => acc + val, 0);
    };
    calculateTotal(cartItems.map((item) => item.giakm * item.soluong))
    const dispatch = useDispatch();
    const [shippingForm] = Form.useForm();
    const [payment, setPayment] = useState('');
    const onFinish = (values) => {
        let newAdd = values.diachi;
        if (districtList.length > 0) {
            const xaPhuong = wardList.filter(item => item.id === values.xa_phuong);
            const quanHuyen = districtList.filter(item => item.id === values.quan_huyen);
            const tinhThanh = cityList.filter(item => item.id === values.tinh_thanh);
            newAdd = values.sonha + ', ' + xaPhuong[0]?._name + ', ' + quanHuyen[0]?._name + ', ' + tinhThanh[0]?._name;
        }
        const invoice = {
            ...values,
            id_user: userInfo[0]?.id_user,
            diachi:newAdd,
            cartItems,
            totalPrice,
        }
        dispatch(createInvoice(invoice));
    };
    function handleChange(value) {
        setPayment(value)
    }
    return (
        <>
            <div className="checkout-shipping-wrapper">
                <div className="checkout-shipping-form">
                    <div className="checkout-fieldset">
                        <Form
                            form={shippingForm}
                            layout="vertical"
                            initialValues={
                                userInfo.length > 0
                                    ? {
                                        hoten: userInfo[0]?.hoten,
                                        email: userInfo[0]?.email,
                                        diachi: userInfo[0]?.diachi,
                                        sodienthoai: userInfo[0]?.sodienthoai,
                                    }
                                    : {
                                        remember: true,
                                    }
                            }
                            onFinish={onFinish}
                        >
                            <Form.Item
                                label="Họ Tên"
                                name="hoten"
                                className="full-name"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập Họ Tên!',
                                    },
                                ]}
                            >
                                <Input placeholder="Họ Tên" />
                            </Form.Item>
                            <Form.Item
                                label="Email"
                                className="form-in"
                                name="email"
                                rules={[
                                    {
                                        type: 'email',
                                        message: 'Email bạn vừa nhập không đúng!',
                                    },
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập Email!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập email của bạn..." />
                            </Form.Item>
                            <Form.Item
                                name="sodienthoai"
                                label="Số điện thoại"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập số điện thoại!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập số điện thoại..." addonBefore="+84" style={{ width: '100%' }} />
                            </Form.Item>
                            {userInfo.length > 0 ? (
                                <Form.Item
                                    name="diachi"
                                    label="Địa Chỉ"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Vui lòng nhập địa chỉ!',
                                        },
                                    ]}
                                >
                                    <Input placeholder="Nhập địa chỉ..." />
                                </Form.Item>
                            ) : (
                                <>
                                    <Form.Item
                                        label="Tỉnh/Thành Phố"
                                        name="tinh_thanh"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập địa chỉ!',
                                            },
                                        ]}
                                    >
                                        <Select

                                            placeholder="Chọn Tỉnh/Thành Phố"
                                            showSearch
                                            filterOption={(input, option) =>
                                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                            }
                                            onChange={(value) => {
                                                dispatch(getDistrictAction(value));
                                                shippingForm.setFieldsValue({
                                                    quan_huyen: undefined,
                                                    xa_phuong: undefined,
                                                })
                                            }}
                                            style={{ textAlign: 'left' }}
                                            allowClear
                                        >
                                            {cityList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>
                                    <Form.Item
                                        label="Quận Huyện"
                                        name="quan_huyen"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập địa chỉ!',
                                            },
                                        ]}
                                    >
                                        <Select
                                            placeholder="Chọn Quận/Huyện"
                                            showSearch
                                            filterOption={(input, option) =>
                                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                            }
                                            onChange={(value) => {
                                                dispatch(getWardAction(value))
                                                shippingForm.setFieldsValue({
                                                    xa_phuong: undefined,
                                                })
                                            }}
                                            style={{ textAlign: 'left' }}
                                            allowClear
                                        >
                                            {districtList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>


                                    <Form.Item
                                        label="Xã Phường"
                                        name="xa_phuong"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập địa chỉ!',
                                            },
                                        ]}
                                    >
                                        <Select
                                            placeholder="Chọn Xã/Phường"
                                            showSearch
                                            filterOption={(input, option) =>
                                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                            }
                                            style={{ textAlign: 'left' }}
                                            allowClear
                                        >
                                            {wardList.map((item, index) => {
                                                return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                            })}
                                        </Select>
                                    </Form.Item>
                                    <Form.Item
                                        label="Số Nhà"
                                        name="sonha"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Vui lòng nhập địa chỉ!',
                                            },
                                        ]}
                                    >
                                        <Input placeholder="Tòa Nhà, Tên Đường..." />
                                    </Form.Item>
                                </>
                            )}

                            <Form.Item
                                label="Phương Thức Thanh Toán"
                                name="loaithanhtoan"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng chọn loại thanh toán...!',
                                    },
                                ]}
                            >
                                <Select placeholder="Chọn phương thức thanh toán..." onChange={handleChange}>
                                    <Select.Option value="cod">COD - Thanh toán khi nhận hàng</Select.Option>
                                    <Select.Option value="online">Thanh toán online - Chuyển khoản</Select.Option>
                                </Select>
                            </Form.Item>
                            {payment === "online"
                                ? <div className="checkout-collapse-sub checkout-fieldset-collapse">
                                    <p>Thông Tin Chuyển Khoản:</p>
                                    <p>VIETCOMBANK - 0041000311172</p>
                                    <p>Tran Ha Nam</p>
                                </div>
                                : null
                            }

                        </Form>
                    </div>
                </div>
            </div>
            {loading && <div style={{ textAlign: 'center' }}>Đang Thanh Toán...</div>}
            <div className="checkout-shipping-action">

                <button onClick={() => history.push(ROUTERS.CHECKOUT_STEP_1)} className="button-muted"
                    type="button"
                >
                    <ArrowLeftOutlined /> &nbsp;
                    Quay Lại
                </button>

                <button onClick={() => shippingForm.submit()} className="next-step-button"
                    type="button"
                >
                    Xác nhận
                    &nbsp;
                    <ArrowRightOutlined />
                </button>
            </div>
        </>
    )
}

export default ShippingForm;
