import React, { useEffect } from 'react'
import { useSelector, useDispatch } from "react-redux";
//CSS
import '../LogInOut/styles.css'
import { Form, Input, Button, Tabs, Select } from 'antd';
//Components
import { registerAction, loginAction, logoutAction, removeCityAction, getCityAction, getDistrictAction, getWardAction, removeWardAction, removeDistrictAction } from '../../redux/actions';
import { ROUTERS } from '../../constants/router';
import { Link } from 'react-router-dom';
function LogInOut() {
    //REDUX 
    const dispatch = useDispatch();
    const user = useSelector(state => state.userReducer);
    const { userInfo, cityList, districtList, wardList } = user;
    //STATE && REF
    //Form Layout & FORM LINH TINH
    const layout = {
        wrapperCol: {
            span: 10,
        },
    };
    const tailLayout = {
        wrapperCol: {

        },
    };
    const { TabPane } = Tabs;

    const onFinishReg = (values) => {
        const xaPhuong = wardList.filter(item => item.id === values.xa_phuong);
        const quanHuyen = districtList.filter(item => item.id === values.quan_huyen);
        const tinhThanh = cityList.filter(item => item.id === values.tinh_thanh);
        const diachi = values.sonha + ', ' + xaPhuong[0]?._name + ', ' + quanHuyen[0]?._name + ', ' + tinhThanh[0]?._name;
        const newValues = {
            ...values,
            diachi,
        }
        dispatch(registerAction(newValues))
    }
    const onFinishLog = (values) => {
        dispatch(loginAction(values))
    };

    useEffect(() => {
        dispatch(getCityAction());
        return () => {
            dispatch(removeCityAction());
            dispatch(removeDistrictAction());
            dispatch(removeWardAction());
        }
    }, []);

    const [newsForm] = Form.useForm()

    return (
        <div className="form container">
            {userInfo.length > 0
                ? <div>
                    <h1>Xin chào {userInfo[0]?.username}</h1>
                    <Button onClick={() => dispatch(logoutAction())}>Log Out</Button>
                </div>
                : <Tabs defaultActiveKey="1">
                    <TabPane tab="Đăng Nhập" key="1">
                        <Form
                            {...layout}
                            name="basic"
                            type="flex"
                            justify="center"
                            align="middle"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinishLog}
                        >
                            <Form.Item
                                className="form-in"
                                name="usernameLog"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your username!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập email/ tên tài khoản..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="passwordLog"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your password!',
                                    },
                                ]}
                            >
                                <Input.Password placeholder="Nhập mật khẩu..." />
                            </Form.Item>
                            <Link to={ROUTERS.FORGOT_PASSWORD}>Quên Mật Khẩu?</Link>
                            <Form.Item {...tailLayout}>
                                <Button className="btn-signin" type="primary" htmlType="submit">
                                    Đăng Nhập
                                </Button>
                            </Form.Item>
                        </Form>
                    </TabPane>
                    <TabPane tab="Đăng Ký" key="2">
                        <Form
                            {...layout}
                            form={newsForm}
                            type="flex"
                            justify="center"
                            align="middle"
                            name="basicReg"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinishReg}
                            scrollToFirstError
                        >
                            <Form.Item
                                className="form-in"
                                name="hoten"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng điền họ tên vào khung',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập họ tên của bạn..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="username"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập tên tài khoản bạn muốn tạo',
                                    },
                                    {
                                        min: 6,
                                        message: 'Tài khoản phải có độ dài ít nhất 6 ký tự'
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập tên tài khoản..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="email"
                                rules={[
                                    {
                                        type: 'email',
                                        message: 'Email bạn nhập không hợp lệ!',
                                    },
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập Email!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhập email của bạn..." />
                            </Form.Item>

                            <Form.Item
                                name="password"
                                className="form-in"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập mật khẩu!',
                                    },
                                    {
                                        min: 8,
                                        message: 'Mật khẩu của bạn phải dài ít nhất 8 ký tự!'
                                    },
                                    {
                                        whitespace: false,
                                        message: 'Vui lòng không dùng khoảng trắng để đặt mật khẩu!'
                                    },
                                ]}
                                hasFeedback
                            >
                                <Input.Password placeholder="Nhập mật khẩu..." />
                            </Form.Item>

                            <Form.Item
                                name="confirm"
                                className="form-in"
                                dependencies={['password']}
                                hasFeedback
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập mật khẩu',
                                    },
                                    {
                                        whitespace: false,
                                        message: 'Vui lòng không dùng khoảng trắng để đặt mật khẩu!'
                                    },
                                    ({ getFieldValue }) => ({
                                        validator(_, value) {
                                            if (!value || getFieldValue('password') === value) {
                                                return Promise.resolve();
                                            }
                                            return Promise.reject(new Error('Mật khẩu bạn vừa nhập không khớp với mật khẩu trước đó!'));
                                        },
                                    }),
                                ]}
                            >
                                <Input.Password placeholder="Xác nhận mật khẩu..." />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="sodienthoai"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập số điện thoại!'
                                    },
                                    {
                                        min: 9,
                                        message: 'Số điện thoại bạn vừa nhập không đúng định dạng'

                                    },
                                    {
                                        required: true,
                                        pattern: new RegExp("^[0-9]*$"),
                                        message: "Số điện thoại chỉ bao gồm số, không bao gồm ký tự khác!"
                                    }
                                ]}
                            >
                                <Input placeholder="Nhập số điện thoại" addonBefore="+84" style={{ width: '100%' }} minLength="9" maxlength="10" />
                            </Form.Item>

                            <Form.Item
                                className="form-in"
                                name="tinh_thanh"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập địa chỉ!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Tỉnh/Thành Phố"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    onChange={(value) => {
                                        dispatch(getDistrictAction(value));
                                        newsForm.setFieldsValue({
                                            quan_huyen: undefined,
                                            xa_phuong: undefined,
                                        })
                                    }}
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {cityList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                className="form-in"
                                name="quan_huyen"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập địa chỉ!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Quận/Huyện"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    onChange={(value) => {
                                        dispatch(getWardAction(value))
                                        newsForm.setFieldsValue({
                                            xa_phuong: undefined,
                                        })
                                    }}
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {districtList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>


                            <Form.Item
                                className="form-in"
                                name="xa_phuong"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập địa chỉ!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Xã/Phường"
                                    showSearch
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    style={{ textAlign: 'left' }}
                                    allowClear
                                >
                                    {wardList.map((item, index) => {
                                        return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                    })}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                className="form-in"
                                name="sonha"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lòng nhập địa chỉ!',
                                    },
                                ]}
                            >
                                <Input placeholder="Tòa Nhà, Tên Đường..." />
                            </Form.Item>

                            <Form.Item {...tailLayout}>
                                <Button className="btn-signin" type="primary" htmlType="submit">Đăng Ký</Button>
                            </Form.Item>
                        </Form>
                    </TabPane>
                </Tabs>
            }

        </div>
    )
}

export default LogInOut
