import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
//React-Redux
import { useSelector, useDispatch } from "react-redux";
import { useEffect } from 'react';
import { getBannerAction, removeBannerAction } from '../../../redux/actions';
import history from '../../../utils/history';
function SliderTitle() {
    const dispatch = useDispatch();
    const getBannerHome = useSelector(state => state.adminReducer);
    const { bannerList } = getBannerHome;
    useEffect(() => {
        dispatch(getBannerAction());
        return () => {
            dispatch(removeBannerAction())
        }
    }, []);
    const settings = {
        dots: false,
        infinite: true,
        fade: false,
        autoplaySpeed: 4000,
        speed: 500,
        arrows: false,
        autoplay: true,
        lazyLoad: 'ondemand',
        pauseOnHover: true,
    };
    return (
        <Slider {...settings} className="slider-home">
            {bannerList.map((item, index) => {
                return (
                    <div className="img-card-home" key={index}>
                        {item.tieude && (
                            <div className="banner-content">
                                <p className={"banner-title animated "+item.title_effect}>{item.title_banner}</p>
                                <p className={"banner-sub-title animated "+item.sub_effect}>{item.sub_title_banner}</p>
                                <button type="button" className={"banner-button button-lazy animated "+item.button_effect} onClick={() => {history.push(`/news/${item.id_tintuc}`)}}>Chi Tiết</button>
                            </div>
                        )}
                        <img className="img-slider" style={{ width: '100%', maxHeight: '800px' }} src={item.url_banner} alt="IMG PRODUCT" />
                    </div>
                )
            })}
        </Slider>
    )
}

export default SliderTitle
