import history from "../../../utils/history"
import StarRatings from 'react-star-ratings';
function Item(props) {
    const { shoeID, shoeName, shoeCode, shoePrice, shoeSale, shoeImg, shoeTypeID, trangthai, phantram, sosao, sobinhluan } = props
    return (
        <div className="col-lg-3 product-col">
            <div className="product-card">


                <div class="product-tumb" onClick={() => history.push(`/products/${shoeID}/${shoeTypeID}`)}>
                {trangthai === "Đang Bắt Đầu"
                    && (
                        <div className="new-label">-{phantram}%</div>
                    )
                }
                    <img src={`/Gallery/${shoeImg}`} alt="" />
                </div>
                <div className="product-details">
                    <h6>{shoeName} - {shoeCode}</h6>
                    <div className="border-divide"></div>
                    <div className="product-bottom-details">
                        {sosao ? (
                            <>
                                <div className="ratings-product-list">
                                    <StarRatings
                                        name='rating'
                                        rating={sosao}
                                        starRatedColor='#fadb14'
                                        starDimension="15px"
                                        starSpacing="2px"
                                    />
                                    <span className="number-comments-prodlist">{sobinhluan}</span>
                                </div>
                            </>
                        ) : (
                            <StarRatings
                                name='rating'
                                rating={0}
                                starRatedColor='#fadb14'
                                starDimension="15px"
                                starSpacing="2px"
                            />
                        )}
                        {trangthai === "Đang Bắt Đầu"
                            ? (
                                <div class="product-price">
                                    <small>{shoePrice.toLocaleString('vn-Vi')} VNĐ</small>
                                    <p>{shoeSale.toLocaleString('vn-Vi')} VNĐ</p>
                                </div>
                            ) : (
                                <div class="product-price">
                                    <p>{shoePrice.toLocaleString('vn-Vi')} VNĐ</p>
                                </div>
                            )
                        }

                    </div>
                </div>
            </div>
        </div >
    );
};
export default Item;