import React, { useEffect } from 'react'
//Components
import { ROUTERS } from '../../constants/router'
import history from '../../utils/history'
//CSS
import { Form, Input, Select } from 'antd';
import { CheckOutlined, ArrowLeftOutlined } from '@ant-design/icons';
//REACT REDUX
import { useSelector, useDispatch } from "react-redux";
//Action
import { updateUserInfoAction, removeCityAction, getCityAction, getDistrictAction, getWardAction, removeWardAction, removeDistrictAction } from '../../redux/actions';
function EditProfile() {
    const dispatch = useDispatch();
    const getUserInfo = useSelector((state) => state.userReducer);
    const { userInfo, cityList, districtList, wardList } = getUserInfo;
    const [editForm] = Form.useForm();
    if (userInfo.length === 0) {
        history.push(ROUTERS.LOGIN)
    }
    useEffect(() => {
        dispatch(getCityAction());
        return () => {
            dispatch(removeCityAction());
            dispatch(removeDistrictAction());
            dispatch(removeWardAction());
        }
    }, []);

    let diachi = userInfo[0]?.diachi;
    let addArray = [];
    for (let i = 0; i < 3; i++) {
        const sonha = diachi.substring(0, diachi.indexOf(','))
        diachi = diachi.slice(diachi.indexOf(',') + 1).trim();
        addArray.push(sonha.trim())
    }
    return (
        <div className="content">
            <div className="profile-holder">
                <div className="user-profile-details">
                    <h4>Thay Đổi Thông Tin Cá Nhân</h4>

                    <Form
                        form={editForm}
                        layout='vertical'
                        initialValues={
                            {
                                hoten: userInfo[0]?.hoten,
                                email: userInfo[0]?.email,
                                sodienthoai: userInfo[0]?.sodienthoai,
                                sonha: addArray[0],
                            }
                        }
                        onFinish={(values) => {
                            const xaPhuong = wardList.filter(item => item.id === values.xa_phuong);
                            const quanHuyen = districtList.filter(item => item.id === values.quan_huyen);
                            const tinhThanh = cityList.filter(item => item.id === values.tinh_thanh);
                            const diachi = values.sonha + ', ' + xaPhuong[0]?._name + ', ' + quanHuyen[0]?._name + ', ' + tinhThanh[0]?._name;
                            const newVal = {
                                ...values,
                                diachi,
                                id_user: userInfo[0]?.id_user,
                            }
                            dispatch(updateUserInfoAction(newVal));
                        }}
                    >
                        <Form.Item
                            label="Họ Tên"
                            name="hoten"
                            className="full-name"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập Họ Tên!',
                                },
                            ]}
                        >
                            <Input placeholder="Họ Tên" />
                        </Form.Item>
                        <Form.Item
                            label="Email"
                            className="form-in"
                            name="email"
                            rules={[
                                {
                                    type: 'email',
                                    message: 'Email bạn vừa nhập không đúng!',
                                },
                                {
                                    required: true,
                                    message: 'Vui lòng nhập Email!',
                                },
                            ]}
                        >
                            <Input placeholder="Nhập email của bạn..." />
                        </Form.Item>
                        <Form.Item
                            name="sodienthoai"
                            label="Số điện thoại"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập số điện thoại!'
                                },
                                {
                                    min: 9,
                                    message: 'Số điện thoại bạn vừa nhập không đúng định dạng'

                                },
                                {
                                    required: true,
                                    pattern: new RegExp("^[0-9]*$"),
                                    message: "Số điện thoại chỉ bao gồm số, không bao gồm ký tự khác!"
                                }
                            ]}
                        >
                            <Input placeholder="Nhập số điện thoại..." addonBefore="+84" style={{ width: '100%' }} minLength="9" maxlength="10" />
                        </Form.Item>
                        <Form.Item
                            label="Tỉnh/Thành Phố"
                            name="tinh_thanh"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập địa chỉ!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Chọn Tỉnh/Thành Phố"
                                showSearch
                                filterOption={(input, option) =>
                                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                onChange={(value) => {
                                    dispatch(getDistrictAction(value));
                                    editForm.setFieldsValue({
                                        quan_huyen: undefined,
                                        xa_phuong: undefined,
                                    })
                                }}
                                style={{ textAlign: 'left' }}
                                defaultValue={diachi}
                                allowClear
                            >
                                {cityList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>
                        <Form.Item
                            label="Quận Huyện"
                            name="quan_huyen"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập địa chỉ!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Chọn Quận/Huyện"
                                showSearch
                                filterOption={(input, option) =>
                                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                onChange={(value) => {
                                    dispatch(getWardAction(value))
                                    editForm.setFieldsValue({
                                        xa_phuong: undefined,
                                    })
                                }}
                                style={{ textAlign: 'left' }}
                                defaultValue={addArray[2]}
                                allowClear
                            >
                                {districtList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>


                        <Form.Item
                            label="Xã Phường"
                            name="xa_phuong"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập địa chỉ!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Chọn Xã/Phường"
                                showSearch
                                filterOption={(input, option) =>
                                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                style={{ textAlign: 'left' }}
                                defaultValue={addArray[1]}
                                allowClear
                            >
                                {wardList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id}>{item._name}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>
                        <Form.Item
                            label="Số Nhà"
                            name="sonha"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập địa chỉ!',
                                },
                            ]}
                        >
                            <Input placeholder="Tòa Nhà, Tên Đường..." />
                        </Form.Item>
                    </Form>
                </div>
                <div className="checkout-shipping-action">

                    <button onClick={() => history.push(ROUTERS.PROFILE)} className="button-muted back-button"
                        type="button"
                    >
                        <ArrowLeftOutlined /> &nbsp;
                    Quay Lại
                </button>

                    <button onClick={() => editForm.submit()} className="next-step-button complete-button"
                        type="button"
                    >
                        <CheckOutlined />
                    &nbsp;
                    Xác nhận
                </button>
                </div>
            </div>
        </div>
    )
}

export default EditProfile
