import { Route, Link, Redirect } from 'react-router-dom';
import { Layout, Menu } from 'antd';
import MenuAdminVertical from '../../pages/AdminCP/MenuAdminVertical'
import logo from '../../img/hgny.png'
import { ROUTERS } from '../../constants/router';
import React, { useState } from 'react';
import './styles.css'
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
} from '@ant-design/icons';
const { Header, Content, Sider } = Layout;
function AdminLayoutVertical(props) {
    const { defaultKey, openKey, exact, path, component: Component, ...other } = props;
    const [isCollapse, setIsCollapse] = useState(false);
    document.title = "Quản Lý | Như Ý"

    const userInfo = JSON.parse(localStorage.getItem('user'));

    if (userInfo.length === 0) {
        return <Redirect to="/login" />
    } else if (userInfo[0]?.role !== 'admin') {
        return <Redirect to="/" />
    }

    return (
        <Route
            exact={exact}
            path={path}
            render={(routeProps) => {
                return (
                    <>
                        <Layout>
                            <Header className="header" style={{ background: '#fff' }}>
                                <div style={{ float: 'left' }}>
                                    <Link to={ROUTERS.HOME}><img width='140px' height='30px' src={logo} alt="loading" /></Link>
                                </div>
                                <Menu mode="horizontal">
                                    {isCollapse ? <MenuUnfoldOutlined style={{ fontSize: '25px', marginLeft: '30px', top: '5px' }} onClick={() => { setIsCollapse(false) }} /> : <MenuFoldOutlined style={{ fontSize: '25px', marginLeft: '30px', top: "5px" }} onClick={() => { setIsCollapse(true) }} />}
                                </Menu>
                            </Header>
                            <Content style={{ padding: '0 50px' }}>
                                <Layout className="site-layout-background" style={{ padding: '24px 0' }} >
                                    <Sider trigger={null} collapsible collapsed={isCollapse} className="site-layout-background" width={200} style={{ background: '#fff' }}>
                                        <MenuAdminVertical defaultKey={defaultKey} openKey={openKey} />
                                    </Sider>
                                    <Content style={{ padding: '0 24px', minHeight: 280, background: '#fff' }}><Component {...other} {...routeProps} /></Content>
                                </Layout>
                            </Content>
                        </Layout>
                    </>
                )
            }}
        />
    );
}

export default AdminLayoutVertical;
