import { Menu } from 'antd';
import { Link } from 'react-router-dom';
import { ROUTERS } from '../../constants/router';

const { SubMenu } = Menu;
export default function MenuAdminVertical(props) {
    const { defaultKey, openKey } = props
    return (
        <Menu selectedKeys={[defaultKey]} mode="inline" defaultOpenKeys={[openKey]}>
            <SubMenu key="shoe" title="Quản Lý Giày">
                <Menu.Item key="readshoe"><Link to={ROUTERS.READ_PRODUCT}>Xem/Sửa Giày</Link></Menu.Item>
                <Menu.Item key="addshoe"><Link to={ROUTERS.ADD_PRODUCT}>Thêm Giày</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="color" title="Quản Lý Màu">
                <Menu.Item key="readcolor"><Link to={ROUTERS.READ_COLOR}>Xem/Sửa Màu</Link></Menu.Item>
                <Menu.Item key="addcolor"><Link to={ROUTERS.ADD_COLOR}>Thêm Màu</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="size" title="Quản Lý Size">
                <Menu.Item key="readsize"><Link to={ROUTERS.READ_SIZE}>Xem/Sửa Size</Link></Menu.Item>
                <Menu.Item key="addsize"><Link to={ROUTERS.ADD_SIZE}>Thêm Size</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="type" title="Quản Lý Kiểu Giày">
                <Menu.Item key="readtype"><Link to={ROUTERS.READ_TYPE}>Xem/Sửa Kiểu Giày</Link></Menu.Item>
                <Menu.Item key="addtype"><Link to={ROUTERS.ADD_TYPE}>Thêm Kiểu Giày</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="sales" title="Quản Lý Khuyến Mãi">
                <Menu.Item key="readsale"><Link to={ROUTERS.READ_SALE}>Xem/Sửa Khuyến Mãi</Link></Menu.Item>
                <Menu.Item key="addsale"><Link to={ROUTERS.ADD_SALE}>Thêm Khuyến Mãi</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="banner" title="Quản Lý Banner">
                <Menu.Item key="readbanner"><Link to={ROUTERS.READ_BANNER}>Xem/Sửa Banner</Link></Menu.Item>
                <Menu.Item key="addbanner"><Link to={ROUTERS.ADD_BANNER}>Thêm Banner</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="news" title="Quản Lý Tin Tức">
                <Menu.Item key="readnews"><Link to={ROUTERS.READ_NEWS}>Xem/Sửa Tin Tức</Link></Menu.Item>
                <Menu.Item key="addnews"><Link to={ROUTERS.ADD_NEWS}>Thêm Tin Tức</Link></Menu.Item>
            </SubMenu>
            <Menu.Item key="order"><Link to={ROUTERS.READ_ORDER}>Quản Lý Hóa Đơn</Link></Menu.Item>
            <Menu.Item key="user"><Link to={ROUTERS.READ_USER}>Quản Lý Người Dùng</Link></Menu.Item>
            <Menu.Item key="statistic"><Link to={ROUTERS.STATISTIC}>Quản Lý Thống Kê</Link></Menu.Item>
        </Menu>
    )
}