//CSS
import { Form, Input, Button, InputNumber, Select, Row, Col, Breadcrumb, Image } from 'antd';
import { Link } from 'react-router-dom';
import { ROUTERS } from '../../../../constants/router';
import { Editor } from '@tinymce/tinymce-react';
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { getShoeAction, getShoeTypeAction, removeShoeAction, removeShoeTypeAction, upLoadFile, updateShoeAction } from "../../../../redux/actions";
import { useEffect, useState } from 'react';
import swal from 'sweetalert';
//COMPONENTS

export default function AdminUpdateProduct(props) {
    const { match } = props
    const [updateForm] = Form.useForm();
    const dispatch = useDispatch();
    const getShoe = useSelector(state => state.productReducer);
    const getShoeType = useSelector(state => state.adminReducer);
    const { shoeList } = getShoe;
    const { shoeTypeList, loading, error } = getShoeType
    //lọc giày theo id , lọc kiểu giày theo id 
    const shoeOnUpdate = shoeList.filter(item => item.id_giay === parseInt(match.params.id))
    //USE EFFECTTTTTTTTT
    useEffect(() => {
        dispatch(getShoeAction())
        dispatch(getShoeTypeAction());
        return () => {
            dispatch(removeShoeAction());
            dispatch(removeShoeTypeAction());
        }
    }, []);
    useEffect(() => { setContent(shoeOnUpdate[0]?.noidung) }, [match.params.id])
    //USE STATEEEEEEEEEEEEEEEEE
    const [selectedFile, setSelectedFile] = useState([]);
    const [content, setContent] = useState('');
    /////////////////////////////
    function onFinish(values) {
        let hinhanh = '';
        let check = false;
        selectedFile.length > 0 ? hinhanh = selectedFile[0].name : hinhanh = shoeOnUpdate[0]?.hinhanh
        const newUpdate = {
            ...values,
            id_giay: match.params.id,
            hinhanh,
            noidung: content,
        }
        if (content === '') {
            swal("Vui lòng điền nội dung", "", "error")
            check = true;
        }
        if(shoeList.some(item => item.tenmagiay.toLowerCase() === values.tenmagiay.toLowerCase())){
            swal("Mã Giày Đã Tồn Tại","Vui lòng nhập mã giày khác","warning")
            check = true;
        }
        if (selectedFile.length > 0 && check === false) {
            const types = ['image/png', 'image/jpeg', 'image/gif']
            if (selectedFile[0].size > 5000000) {
                swal("Kích Thước Ảnh Quả Lớn", "vui lòng chọn file ảnh có kích thước nhỏ hơn 5mb", "warning")
            }
            if (types.every(type => selectedFile[0].type !== type)) {
                swal("Sai Định Dạng", "vui lòng chọn file ảnh theo định dạng PNG,JPG,JPEG", "warning")
            }
            else{
                dispatch(updateShoeAction(newUpdate));
                dispatch(upLoadFile(selectedFile));
            }
        } 
        if(selectedFile.length === 0 && check === false) {
            dispatch(updateShoeAction(newUpdate));
        }
    }
    function updateImage(e) {
        setSelectedFile(e.target.files)
    }
    function handleEditorChange(content, editor) {
        setContent(content)
    }

    return (
        <>
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4>Cập Nhật Giày</h4>
            </div>
            <Breadcrumb style={{ marginTop: '20px', marginLeft: '20px' }}>
                <Breadcrumb.Item><Link to={ROUTERS.READ_PRODUCT}>Xem Giày</Link></Breadcrumb.Item>
                <Breadcrumb.Item>Cập Nhật Giày</Breadcrumb.Item>
            </Breadcrumb>
            <Row style={{ marginTop: '30px' }}>
                <Col span={12} offset={6}>
                    <Form
                        form={updateForm}
                        initialValues={{
                            tengiay: shoeOnUpdate[0]?.tengiay,
                            tenmagiay: shoeOnUpdate[0]?.tenmagiay,
                            giaban: shoeOnUpdate[0]?.giaban,
                            id_kieugiay: shoeOnUpdate[0]?.id_kieugiay,
                            tinhtrang: shoeOnUpdate[0]?.tinhtrang.toString(),
                        }}
                        onFinish={onFinish}
                        layout='vertical'
                    >
                        <Form.Item
                            name="tengiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập tên giày...',
                                },
                            ]}
                        >
                            <Input placeholder="Nhập tên giày..." />
                        </Form.Item>

                        <Form.Item

                            name="tenmagiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập tên mã giày...',
                                },
                            ]}
                        >
                            <Input placeholder="Nhập tên mã giày..." />
                        </Form.Item>

                        <Form.Item
                            label="Giá Bán: "
                            name="giaban"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng nhập giá bán...',
                                },
                            ]}
                        >
                            <InputNumber
                                style={{ width: 'auto' }}
                                min={1}
                                max={10000000}
                                formatter={value => `${value} VNĐ`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                parser={value => value.replace(/\VNĐ\s?|(,*)/g, '')}
                            />
                        </Form.Item>

                        <Editor
                            apiKey='vay56zrvh9veuhcbway7pupzaoycpwvx06k3t4xzzsv3lfgv'
                            initialValue={shoeOnUpdate[0]?.noidung}
                            init={{
                                height: 500,
                                menubar: true,
                                plugins: [
                                    'advlist autolink lists link image charmap print preview anchor',
                                    'searchreplace visualblocks code fullscreen',
                                    'insertdatetime media table paste code help wordcount'
                                ],
                                toolbar:
                                    'undo redo | formatselect | bold italic backcolor | \
                                                alignleft aligncenter alignright alignjustify | \
                                                bullist numlist outdent indent | removeformat | help'
                            }}
                            onEditorChange={handleEditorChange}
                        />

                        <Form.Item
                            name="id_kieugiay"
                            style={{ marginTop: '20px' }}
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng chọn kiểu giày...',
                                },
                            ]}
                        >
                            <Select>
                                {shoeTypeList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id_kieugiay}>{item.tenkieugiay}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>

                        <Form.Item
                            name="tinhtrang"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lòng chọn tình trạng...',
                                },
                            ]}
                        >
                            <Select>
                                <Select.Option value="1">Kích Hoạt</Select.Option>
                                <Select.Option value="0">Không Kích Hoạt</Select.Option>
                            </Select>
                        </Form.Item>

                        <div class="form-group files">
                            <label>Hình Ảnh Đại Diện</label>
                            <input type="file" class="form-control" onChange={updateImage} />
                        </div>
                        {loading
                            ? <div>Uploading...</div>
                            : error
                                ? <div>{error}</div>
                                : <div>Please Choose Image</div>}
                        <Image
                            height={300}
                            width={450}
                            src={`/gallery/${shoeOnUpdate[0]?.hinhanh}`}
                        />
                        <Form.Item >
                            <Button type="primary" htmlType="submit">
                                LƯU
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        </>
    )
}