import { Row, Image, Col, Table, Space } from "antd";
import { DeleteTwoTone,EditTwoTone } from '@ant-design/icons';
import swal from "sweetalert";
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { useEffect } from 'react';
import { getBannerAction, removeBannerAction, deleteBannerAction } from "../../../redux/actions";
import history from "../../../utils/history";

//

const { Column } = Table;
export default function AdminDisplayBanner() {
    const dispatch = useDispatch();
    const getBannerContent = useSelector(state => state.adminReducer);
    const { bannerList } = getBannerContent;
    useEffect(() => {
        dispatch(getBannerAction());
        return () => {
            dispatch(removeBannerAction())
        }
    }, []);

    function deleteBanner(id_banner){
        swal({
            title: "Bạn Có Chắc Chắn Không?",
            text: "Một khi xóa, bạn sẽ không thể khôi phục lại file này",
            icon: "warning",
            buttons: true,
            dangerMode: true,
          })
          .then((willDelete) => {
            if (willDelete) {
              swal("Xóa Thành Công", {
                icon: "success",
              });
              dispatch(deleteBannerAction(id_banner))
            } else {
              swal("Dữ Liệu Của Bạn Đã Được Bảo Toàn!");
            }
          });
    }
    return (
        <Row style={{ marginTop: '30px' }}>
            <div style={{ textAlign: 'center', width: '100%' }}>
                <h4>Quản Lý Banner</h4>
            </div>
            <Col span={18} offset={3}>
                <Table dataSource={bannerList} pagination={{ position: ['bottomCenter'] }} scroll={{x:1500}}>
                    <Column
                        title="Hình Ảnh"
                        key="image"
                        render={(text, record) => (
                            <Image
                                width={200}
                                src={record.url_banner}
                            />
                        )}
                    />
                    <Column title="Tiêu Đề Ảnh" dataIndex="title_banner" key="title_banner" />
                    <Column title="Tiêu Đề Phụ" dataIndex="sub_title_banner" key="sub_title_banner" />
                    <Column title="Hiệu Ứng Tiêu Đề" dataIndex="title_effect" />
                    <Column title="Hiệu Ứng Tiêu Đề Phụ" dataIndex="sub_effect" />
                    <Column title="Hiệu Ứng Button" dataIndex="button_effect" />
                    <Column title="Tin Tức" dataIndex="tieude" />
                    <Column
                        title="Action"
                        key="action"
                        render={(text, record) => (
                            <Space size="middle">
                                <EditTwoTone onClick={()=>history.push(`/readbanner/editbanner/${record.id_banner}`)} twoToneColor='#eb2f96' style={{ fontSize: '30px' }} />
                                <DeleteTwoTone onClick={() => deleteBanner(record.id_banner)} twoToneColor='red' style={{ fontSize: '30px' }} />   
                            </Space>
                        )}
                    />
                </Table>
            </Col>
        </Row>
    )
}